'use client';
import "../../../app/global.scss";

import React from 'react';
import useUserStore from "@/store/users"; 
import styles from "./UserProfile.module.scss";

const UserProfile = () => {
  const { user } = useUserStore();

  if (!user) return <div>Kullanıcı verisi bulunamadı.</div>;

  return (
    <div className={styles.userProfile}>
      <h2 className={styles.title}>Kullanıcı Profili</h2>

      <p className={styles.desc}>Kullanıcı bilgilerinizi aşağıda görüntüleyebilirsiniz.</p>

      <div className={styles.avatarWrapper}>
        {user.avatarUrl ? (
          <img src={user.avatarUrl} alt="Avatar" className={styles.avatar} />
        ) : (
          <div className={styles.avatarPlaceholder}>Avatar Yok</div>
        )}
      </div>

      <form className={styles.form}>
        <label>
          Email:
          <input type="text" value={user.email} readOnly />
        </label>

        <label>
          Nickname:
          <input type="text" value={user.nickname} readOnly />
        </label>

        <label>
          UID:
          <input type="text" value={user.uid} readOnly />
        </label>

        <label>
          UID Code:
          <input type="text" value={user.uidCode} readOnly />
        </label>

        <label>
          Telefon Numarası:
          <input type="text" value={`+${user.countryCode} ${user.phoneNumber}`} readOnly />
        </label>

        
      </form>
    </div>
  );
};

export default UserProfile;
